* {
    box-sizing: border-box;
  }
body {
    background-color: rgb(214, 214, 214) ;

    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    min-height: 100vh;
    margin: 0;
    font-family: 'Lato', sans-serif;
}
#root {
    -webkit-box-shadow: 0px 0px 16px 9px rgba(255, 255, 255, 0.6);
    -moz-box-shadow: 0px 0px 16px 9px rgba(255, 255, 255, 0.6);
    box-shadow: 0px 0px 16px 9px rgba(255, 255, 255, 0.6);

    border-radius: 25px;
}

.app {
    display: flex;
    align-items: center;
    flex-direction: column;

    padding: 20px;

    width: 500px;
    max-width: 500px;

    background-color: rgb(218, 218, 218);
    border-radius: 25px;

}

/* Form Form Form Form Form Form Form Form Form Form */

.form {
    width: 100%;
    display: flex;
    justify-content: space-between;

    margin-bottom: 20px;
}
.form button {
    background: rgb(138, 43, 226);
    outline: none;
    border: none;
    cursor: pointer;
    font-size: 20px;
    font-weight: bold;
    padding: 10px 15px;

    border-radius: 10px;
}
.form button:active {
    background: rgba(137, 43, 226, 0.515);
}
.form input {
    width: 100%;
    margin-right: 10px;
    outline: none;
    text-align: center;
    border: 1px solid gold;
    padding: 10px;
    font-size: 16px;

    border-radius: 10px;
}

/* List List List List List List List List List List */

.todo-list {
    width: 100%;
}

ul {
    padding: 0px;
}

.todo-item {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 10px;
    background: white;
    border-radius: 10px;
    
    margin-bottom: 10px;
}

.todo-item-box {
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: space-between;
}
.todo-item-box input {
    cursor: pointer;
    min-width: 20px;
    min-height: 20px;
    margin-right: 15px;
}
.todo-item-box button:first-child {
    background-color: rgb(56, 163, 65);
    outline: none;
    border: none;
    cursor: pointer;
    border-radius: 10px;
    margin-right: 10px;
}
.todo-item-box button:first-child:active {
    background-color: rgba(56, 163, 65, 0.508);
}
.todo-item-box button {
    background-color: rgb(237, 46, 46);
    outline: none;
    border: none;
    cursor: pointer;
    border-radius: 5px;
    padding: 5px 8px;

    font-weight: bold;
}
.todo-item-box button:active {
    background-color: rgba(237, 46, 46, 0.522);
}
